import React from "react";
import api from "../api";
import Link from "next/link";

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      post: {}
    };
  }

  componentDidMount() {
    const { label } = this.props;
    api
      .byPage({
        labels: label,
        type: "PAGE"
      })
      .then(data => {
        if (data.length) {
          this.setState(() => ({
            post: data[0]
          }));
        }
      });
  }

  render() {
    return <div className="loading">Loading...</div>;
  }
}
